@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open-Sans";
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  src: url("../fonts/opensanslight.woff2") format("woff2");
  font-display: swap;
}
.main-body {
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  font-style: light;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  width: 100%;
  background-color: #173c70;
  background-image: url("/img/back-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
}

.main-header__navigation-block {
  padding: 22px 20px 21px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.3);
}

.main-header__navigation-block-wrapper {
  display: flex;
  justify-content: space-between;
}

.main-header__slogan {
  font-size: 24px;
  line-height: 29px;
  color: #ffffff;
  text-align: center;
  padding: 0 20px;
  margin: 30px 0 34px;
}

.main-header__image {
  display: block;
}

@media (min-width: 1px) and (max-width: 1999px) {
  .main-header__open-navigation-list-wrapper {
    display: none;
    position: absolute;
    width: 100%;
    top: 0;
    right: 0;
  }
}
@media (min-width: 660px) {
  .main-header__image {
    display: none;
  }

  .main-header__wrapper {
    background-image: url("/img/phone-in-hand-tablet.svg");
    background-repeat: no-repeat;
    background-position: bottom;
    padding-top: 132px;
  }

  .main-header__slogan {
    width: 300px;
    font-size: 40px;
    line-height: 48px;
  }
}
@media (min-width: 1200px) {
  .main-navigation-toggle {
    display: none;
  }

  .main-header__navigation-block {
    flex-direction: row;
    justify-content: space-between;
    padding: 22px 130px 21px;
  }

  .main-header__open-navigation-list-wrapper {
    display: flex;
    flex-direction: row;
    position: sticky;
  }

  .main-header__wrapper {
    padding: 0 130px;
  }

  .main-header__slogan {
    width: 300px;
  }
}
